:root {
  --height540: 250px;
  --height768: 300px;
  --height960: 350px;
  --height1140: 400px;
  --heightbig: 450px;
}


@media screen  and (max-width: 540px) {
  .app-container {
    height:  var(--height540);
  }
}

@media (min-width: 541px) and (max-width:768px) {
  .app-container {
    height:  var(--height768);
  }
}

@media(min-width: 769px) and (max-width: 960px) {
  .app-container {
    height:  var(--height960);
  }
}

@media(min-width: 961px) and (max-width: 1140px) {
  .app-container {
    height:  var(--height1140);
  }
}

@media(min-width: 1141px) {
  .app-container {
    height:  var(--heightbig);
  }
}

